$backgroundColor: #f5f5f5;

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: $backgroundColor;
  height: 100%;

  #root {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  footer {
    background: darken($backgroundColor, 50);
    border-top: 1px solid #eee;
    padding: 1em 3em;
    margin: 0;
  }

  header {
    background: darken($backgroundColor, 75);
    border-bottom: 1px solid #eee;
    color: #eee;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  #requestMethod, #url {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 22px;
    color: rgb(105, 105, 105);
    text-align: center;
    margin: 5px;
  }
}
